export default {
	data() {
		return {
			goodsList: [],
			goodsPrice: 0.0, // 商品金额
			sumPrice: 0.0, // 用户支付金额
			freight: 12.0 // 运费
		};
	},
	onShow() {
		// 将本地存储中的数据获取到
		uni.getStorage({
			key: 'confirmList',
			success: res => {
				this.goodsList = res.data;

				for (let i = 0; i < this.goodsList.length; i++) {
					this.goodsPrice = this.goodsPrice + this.goodsList[i].number * this.goodsList[i].price;
				}

				// 用户支付的金额
				this.sumPrice = this.goodsPrice + this.freight;
			}
		});
	},
	filters: {
		toFixed(x) {
			return parseFloat(x).toFixed(2);
		}
	},
	onBackPress() {
		// 页面回退 清楚订单信息
		this.clearOrder();
	},
	methods: {
		clearOrder() {
			uni.removeStorage({
				key: 'confirmList',
				success: res => {
					this.goodsList = [];
				}
			});
		}
	}
};
